:root {
  --fontText: 'museo_sans500', serif;
}
@font-face {
    font-family: 'museo_sans100';
    src: url('fonts/exljbris_-_museosans-100-webfont.woff2') format('woff2'),
         url('fonts/exljbris_-_museosans-100-webfont.woff') format('woff'),
         url('fonts/exljbris_-_museosans-100-webfont.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;

}




